import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams} from 'ionic-angular';
import { DirectorProvider } from '../../providers/director/director';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
import { ProfileProvider } from '../../providers/profile/profile';
import { PopUpProvider } from '../../providers/pop-up/pop-up';
/**
 * Generated class for the RestaurantDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-currentfood-details',
  templateUrl: 'currentfood-details.html',
})
export class CurrentfoodDetailsPage {



  public resImage: any;
  public rate: number = 4;
  public details: string = "menus";
  public location_Name;
  public items: any;
  public categories: any;
  public number: any = 0;
  public size: any;
  public go: any;
  drink: any ='none';
  trolls: string;
  juice: any = false;
  toggleMore: any;
  cont: any = [];
  quantity: any = 0;
  hasFood: any;
  foodPrice: any;
  stush: any;
  constructor(public gCode: GeocoderProvider, public ph: ProfileProvider, public pop: PopUpProvider, public director: DirectorProvider, public navCtrl: NavController, public navParams: NavParams) {
    this.resImage =  director.currentfood_Detail.image
  }


  ionViewDidLoad(){
    
    
    if (this.ph.hasCalled){
      this.increase();
      this.ph.hasCalled = false;

    }


    this.ph.getUserProfile().child('Basket').child(this.director.restuarant_Detail.key).child(this.director.currentfood_Detail.name).on('value', usereSnapshot => {
      if (usereSnapshot.val()){
      this.foodPrice = usereSnapshot.val().price
      }else{
        this.foodPrice = 0
      }
    })
  //  this.pop.presentLoader('')
  this.ph.getUserProfile().child('Basket').child(this.director.restuarant_Detail.key).child(this.director.currentfood_Detail.name).on('value', ueSnapshot => {
    if (ueSnapshot.val()){
   this.quantity = ueSnapshot.val().quantity;
    }else{
      this.quantity = 0;
    }
  })
 

    this.ph.getUserProfile().child('Drink').child(this.director.restuarant_Detail.key).child('drinks').on('value', ProfileSnapshot => {
      this.items = []

      if (ProfileSnapshot.val()){
     //   this.pop.hideLoader()
      ProfileSnapshot.forEach( snap => {
       
        this.juice = true;
       this.items.push({
         key: snap.key,
         name: snap.val().name,
         price: snap.val().price,
         checked: snap.val().checked,
       });

      
       console.log(this.items)
       return false
     
     });
    }else{
      this.ph.getResturantProfile().child(this.director.restuarant_Detail.key).child('category').child(this.director.food_Detail.key).child('drinks').on('value', userProfileSnapshot => {
        console.log(userProfileSnapshot.val(), this.stush)
    
         console.log(userProfileSnapshot.val())
       userProfileSnapshot.forEach( snap => {
     
            this.ph.getUserProfile().child('Drink').child(this.director.restuarant_Detail.key).child('drinks').child(snap.key).update({
             name: snap.val().name,
             price: snap.val().price,
             checked: snap.val().checked,
           })
 
           return false
       
           
         
         });
       
     })
    }
    })

   
  }

  sizeCheck(){
   console.log(this.size)
  }
  



  increase(){
    console.log(this.quantity);
   this.hasFood = true;
    this.quantity = this.quantity + 1;

    let churn = this.director.currentfood_Detail.price*this.quantity
       this.ph.getUserProfile().child('Basket').child(this.director.restuarant_Detail.key).child(this.director.currentfood_Detail.name).update({
        image: this.director.currentfood_Detail.image,
        price: parseInt(this.director.currentfood_Detail.price) * this.quantity,
        name:  this.director.currentfood_Detail.name,
        quantity: this.quantity,
        restuarantKey: this.director.restuarant_Detail.key
      }).then((rop)=>{

        
        // console.log(rop)
        if (this.director.total){
             console.log(this.director.total)
          this.ph.getUserProfile().child('Basket').update({
          total: parseInt(this.director.total) + parseInt(this.director.currentfood_Detail.price)
        })

        }else{
          this.ph.getUserProfile().child('Basket').update({
            total: parseInt(this.director.currentfood_Detail.price)
          })
        }
        
      
      })





  }

  selectDrink(item){
    if (this.quantity > 0){
    console.log(item)
   let ref = this.ph.getUserProfile().child('Drink').child(this.director.restuarant_Detail.key).child('drinks').child(item.key)
   let ref2 = this.ph.getUserProfile().child('Basket').child(this.director.restuarant_Detail.key).child(this.director.currentfood_Detail.name)
   
   
   
   let gh = item.key
   let r = item.name
   ref.once('value', userProfileSnapshot => {

          if (userProfileSnapshot.val().checked){
            ref.off('value')
            ref.update({
              checked: false
            })
            console.log('unchecked')
            ref2.child('drink').once('value', userProfileSnapshot => {
              console.log(userProfileSnapshot.val())
             ref2.update({
               drink: userProfileSnapshot.val() - item.price
             }).then(()=>{
              
             
              ref2.child('drinks').child(item.name).remove()
                  

              
             
                
                this.ph.getUserProfile().child('Basket').update({
                total: this.director.total - item.price
              })
           
      
             })
           
           })
            
         }



          if (userProfileSnapshot.val().checked == false){
            ref.off('value')
            ref.update({
              checked: true
            })
            console.log('checked')
           ref2.child('drink').once('value', userProfileSnapshot => {
             console.log(parseInt(userProfileSnapshot.val()))
             console.log(parseInt(item.price))
             let ru = parseInt(userProfileSnapshot.val()) + parseInt(item.price)
             if (userProfileSnapshot.val() != null){
            ref2.update({
              drink: ru
            }).then(()=>{
              ref2.child('drinks').push({
                name: item.name
              })
              if (this.director.total){
              this.ph.getUserProfile().child('Basket').update({
                total: this.director.total + parseInt(item.price)
              })
            }else{
              this.ph.getUserProfile().child('Basket').update({
                total: parseInt(item.price)
              })
            }
            })
          }else{
            ref2.update({
              drink: parseInt(item.price)
            }).then(()=>{
              ref2.child('drinks').push({
                name: item.name
              })
              if (this.director.total){
              this.ph.getUserProfile().child('Basket').update({
                total: this.director.total + parseInt(item.price)
              })
            }else{
              this.ph.getUserProfile().child('Basket').update({
                total: parseInt(item.price)
              })
            }
            })
          }
          })
       
          }

        

        })

      }else{
        this.pop.showPimp('Choose Food First')
      }
      }


  decrease(){
    this.quantity = this.quantity - 1
  

      if (this.quantity >= 1){

  
    if (this.quantity != 0){
      
      this.ph.getUserProfile().child('Basket').child(this.director.restuarant_Detail.key).child(this.director.currentfood_Detail.name).update({
       image: this.director.currentfood_Detail.image,
       price: parseInt(this.director.currentfood_Detail.price) - parseInt(this.director.currentfood_Detail.price),
       name:  this.director.currentfood_Detail.name,
       quantity: this.quantity,
       restuarantKey: this.director.restuarant_Detail.key
     }).then(()=>{
    
      if (this.director.total){
        this.ph.getUserProfile().child('Basket').update({
        total: parseInt(this.director.total) - parseInt(this.director.currentfood_Detail.price)
      })

      }else{
        this.ph.getUserProfile().child('Basket').update({
          total: this.director.currentfood_Detail.price*this.quantity
        })
      }
      
    
    })
  }else{
    console.log('empty')
   // this.ph.getUserProfile().child('Basket').child(this.director.restuarant_Detail.key).child(this.director.currentfood_Detail.name).remove()
  }



    }


  }


  Accept(){
    this.navCtrl.push('BasketPage')
  }

  gotoCard(){
    console.log(this.go)
  }

}



